.createcategory{
    box-shadow:
    0px 4.4px 10.5px rgba(0, 0, 0, 0.059),
    0px 12.3px 29.1px rgba(0, 0, 0, 0.085),
    0px 29.5px 69.9px rgba(0, 0, 0, 0.111),
    0px 98px 232px rgba(0, 0, 0, 0.17)
  ;
    border-radius:50px;
    width: 80%;
    align-items: center;
    align-self: center;
    justify-content: center;
    margin: auto;
    height: 45rem;
    background: white;
}

.colorPicker{
    display: flex;
    justify-content: center;
    align-items: center;
}

.createcategory__input{
    background-color: #f0f0f0;
    margin: 10px 0;
    height: 55px;
    border-radius: 55px;
    display: grid;
    grid-template-columns: 15% 85%;
    padding: 0 0.4rem;
}

.createcategory__input i {
    text-align: center;
    line-height: 55px;
    color: #acacac;
    transition: 0.5s;
    font-size: 1.1rem;  
}
  
.createcategory__input input {
    background: none;
    outline: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333;
}
  
.createcategory__input input::placeholder {
    color: #aaa;
    font-weight: 500;
}